const gallery = document.querySelector('.gallery');

const imgMarkup = images
  .map(
    imagen =>
      `<li class="gallery-item">
  <a class="gallery-link" href="${imagen.original}">
    <img
      class="gallery-image"
      src="${imagen.preview}"
      data-source="${imagen.original}"
      alt="${imagen.description}"
    />
  </a>
</li>
`
  )
  .join('');

gallery.innerHTML = imgMarkup;

const galleryItem = gallery.querySelectorAll('.gallery-item');
galleryItem.forEach(item => {
  item.style.width = 'calc((100% - 48px) / 3)';
  item.style.boxSizing = 'border-box';
  item.style.maxHeight = '200px';
  item.style.maxWidth = '360px';
});

const galleryImg = gallery.querySelectorAll('.gallery-image');
galleryImg.forEach(img => {
  img.style.width = '100%';
  img.style.height = '100%';
  img.style.display = 'block';
});

gallery.addEventListener('click', onClick);

function onClick(e) {
  e.preventDefault();
  const mainImage = e.target.dataset.source;
  if (e.target.nodeName !== 'IMG') {
    return;
  }
  new SimpleLightbox('.gallery a', { loop: false });
}
